// ## fonts
// Save fonts to /src/fonts
+font(segoeuii, segoeuii)
=segi
	font-family: "segoeuii", Arial, sans-serif
+font(segoeuil, segoeuil)
=segl
	font-family: "segoeuil", Arial, sans-serif
+font(PTRouble-sans, PTRouble-sans)
=rub
	font-family: "PTRouble-sans", Arial, sans-serif
+font(pfdindisplaypro-light, pfdindisplaypro-light)
=pfdl
	font-family: "pfdindisplaypro-light", Arial, sans-serif
=roboto
	font-family: 'Roboto', Arial, sans-serif
=light
	font-weight: 300
=reg
	font-weight: 400 
=bold
	font-weight: 700 
body
	+roboto
	+light
	font-size: 1.5rem
	line-height: 1.4
	color: $text 
	background: #fbf8da url(../img/body-bg.png) 0 0 repeat
// common blocks
.rub
	+rub
.inner
	position: relative
	max-width: 1280px
	margin: 0 auto
	&_main
		padding: 27px 0 40px
		background: #fff 
.title
	+light
	line-height: 1.4
	&_h1
		font-size: 40px	
	&_h2
		font-size: 30px
	&_h3
		font-size: 20px

.number
	position: relative
	display: inline-block
	vertical-align: middle
	height: 26px
	box-sizing: border-box
	padding: 2px 25px 2px 2px 
	&__input
		display: inline-block
		height: 22px
		width: 100px
		text-align: center
		font-size: 18px
	&__plus,
	&__minus
		width: 22px
		height: 22px
		color: #fff
		background: #000
.btn-dots
	display: inline-block
	line-height: 1.1
	background: none 
	border-bottom: 1px dotted
	+roboto
	+reg
	&:hover	
		border-color: transparent
		text-decoration: none
.btn-danger
	border: 0
	padding: 10px 25px
	line-height: 1
	+roboto
	+light
	font-size: 20px
	background: $promo
	+tr(background .3s)
	&:hover
		background: lighten($promo, 5%) 
	&:focus,
	&:active
		background: lighten($promo, 10%)
.btn-info
	border: 2px solid $btn
	background: none
	text-shadow: none
	box-shadow: none
	color: $btn
	transition: color, background, border, .3s
	&:hover
		background: $primary
		color: #fff  
		border-color: $primary
	&:active,
	&:focus
		background: darken($primary, 5%)
		border-color: darken($primary, 5%)
		color: #fff
		box-shadow: none 

.form-action
	position: relative
	&__table
		display: table
		width: 100%
	&__cell
		display: table-cell
		vertical-align: middle
	&__input
		display: inline-block
		vertical-align: middle
		padding: 0 12px 
		width: 100%
		height: 21px
		background: #fff
		font-size: 13px
		+roboto
		+reg
		color: $text 
		+placeholder
			color: #959595 
	&__btn
		display: inline-block
		vertical-align: middle
		padding: 0 8px
		height: 21px
		+roboto
		+reg
		font-size: 13px
		background: $btn
		line-height: 1  
	label
		+reg
.tel
	text-align: right
	font-size: 20px
	line-height: 1
	.btn-dots
		font-size: 13px
	span
		display: block
.payments
	margin-bottom: 15px
	font-size: 13px
	&__list
		padding-top: 10px 
		font-size: 0
		img
			display: inline-block
			margin-right: 4px
			vertical-align: top 
.visit-card
	li
		margin-bottom: 15px 
.soc
	font-size: 28px
	a
		display: inline-block
		background: #fff 
		+tr(color .3s)
		&:hover
			text-decoration: none
.i-icon-fb
	color: #4065b4
	&:hover
		color: darken(#4065b4, 5%)
.i-icon-tw
	color: #39b9dc
	&:hover
		color: darken(#39b9dc, 5%)
.i-icon-in
	color: #157fb9
	&:hover
		color: darken(#157fb9, 5%)
.i-icon-vk
	color: #4e759d
	&:hover
		color: darken(#4e759d, 5%)
.i-icon-yt
	color: #c13a35
	&:hover
		color: darken(#c13a35, 5%)
.tabs-link
	margin: 0
	line-height: 1.2
	font-size: 15px
	+light
	li
		display: inline-block
		margin-right: 25px
		vertical-align: middle
		&.active
			a
				border-color: transparent
				color: $text
				+bold  
	a
		display: inline-block
		border-bottom: 1px dotted 
		+tr(border .3s)
		&:hover
			text-decoration: none
			border-color: transparent 
.tabs-view
	margin: 0
	padding: 0 20px 
	line-height: 1.2
	font-size: 15px
	+light
	li
		display: inline-block
		margin-left: 25px
		vertical-align: middle
		&.active
			span
				border-color: transparent
				color: $text
				+bold  
	a
		display: inline-block
		&:hover
			text-decoration: none
	span
		&:last-child
			display: inline-block
			border-bottom: 1px dotted 
			+tr(border .3s)
			&:hover
				text-decoration: none
				border-color: transparent 
.post
	&__img
		margin-bottom: 23px
		img
			display: block
			width: 100%
			height: auto
	&__main
		line-height: 1.66
		font-size: 15px
		+light