@font-face
	font-family: "svgfont"
	src: url('fonts/svgfont.eot')
	src: url('fonts/svgfont.eot?#iefix') format('eot'), url('fonts/svgfont.woff') format('woff'), url('fonts/svgfont.woff2') format('woff2'), url('fonts/svgfont.ttf') format('truetype')
	font-weight: normal
	font-style: normal

$i-icon-sepa: "\EA01"
$i-icon-skrill: "\EA02"
$i-icon-tw: "\EA03"
$i-icon-yt: "\EA04"
$i-icon-fb: "\EA05"
$i-icon-in: "\EA06"
$i-icon-vk: "\EA07"
$i-icon-cart: "\EA08"


%i-icon
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale

[class^="i-icon-"], [class*=" i-icon-"]
	@extend %i-icon
	display: inline-block

.i-icon-sepa:before
	content: $i-icon-sepa
.i-icon-skrill:before
	content: $i-icon-skrill
.i-icon-tw:before
	content: $i-icon-tw
.i-icon-yt:before
	content: $i-icon-yt
.i-icon-fb:before
	content: $i-icon-fb
.i-icon-in:before
	content: $i-icon-in
.i-icon-vk:before
	content: $i-icon-vk
.i-icon-cart:before
	content: $i-icon-cart